	module.exports = {"data":{"wordpressPost":{"title":"The Hot New Makeup Trend Of 2017 &#8211; Say Goodbye To Contouring","content":"<p>Just this week I heard about <strong>‘nontouring’</strong>. That’s right &#8211; contouring &#8211; except not. Yes, it seems that Kim Kardashian’s major hot trend of contouring, that is applying darker coloured makeup to the areas of the face you wish to recede and lighter, more shimmery products to the features you wish to highlight, is officially over.</p>\n<p>And now it&#8217;s all about nontouring &#8211; that is, applying one shade of cover (think foundation, tinted moisturiser etc) and that&#8217;s it. No darker shades in the hollows of your cheeks, no lighter shades at the high points of the cheekbones &#8211; just one flattering shade that works for you.</p>\n<p>Do I care? Well, a little bit &#8211; but um &#8230; maybe not as much as you might think. The thing to note about this is that beauty trends come and go all the time. My advice &#8211;<strong> choose those that make you feel beautiful</strong> and stick with them as long as you like. Yep. Even if they&#8217;re officially done and dusted. Whether Kim Kardashian is on board is neither here nor there.</p>\n<p>So, in all seriousness, while <strong>&#8216;nontouring&#8217;</strong> is supposedly going to be the next big thing it really doesn&#8217;t matter. Because you, my lovely friends, can rock any lock your heart desires. That said, if <strong>nontouring</strong> sounds good to you, here&#8217;s a couple of products that will help you bring that look to life.</p>\n<p>&nbsp;</p>\n<img class=\"size-medium wp-image-2563 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220541/Nontouring-UB-JPG-300x210.jpg\" alt=\"\" width=\"300\" height=\"210\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220541/Nontouring-UB-JPG-300x210.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220541/Nontouring-UB-JPG-768x536.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220541/Nontouring-UB-JPG.jpg 839w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://www.mecca.com.au/urban-decay/naked-skin-shapeshifter/V-027369.html?cgpath=whatsnew-makeup#start=1\">Urban Decay Naked Skin Shapeshifter, $70. </a>Yes, it&#8217;s actually designed for contouring, but, you can stick with just one to three shades and create a lovely, natural, nontoured look too.</p>\n<img class=\"size-medium wp-image-2564 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220707/Smashbox-JPG-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220707/Smashbox-JPG-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220707/Smashbox-JPG-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220707/Smashbox-JPG-768x767.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220707/Smashbox-JPG.jpg 920w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://www.mecca.com.au/smashbox/baked-fusion-soft-lights/I-007611.html\">Smashbox Baked Fusion Soft Lights, $46.</a> Boost radiance and add glow all over with this silk-like powder.</p>\n<img class=\"size-medium wp-image-2565 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220749/LM-Candleglow-Foundation-1-150x300.jpg\" alt=\"\" width=\"150\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220749/LM-Candleglow-Foundation-1-150x300.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220749/LM-Candleglow-Foundation-1-768x1536.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220749/LM-Candleglow-Foundation-1-512x1024.jpg 512w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08220749/LM-Candleglow-Foundation-1.jpg 900w\" sizes=\"(max-width: 150px) 100vw, 150px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://shop.davidjones.com.au/djs/en/davidjones/candleglow-foundation\">Laura Mercier Candleglow Foundation, $65.</a> Ultra-lightweight, sheer to medium formula that glides on leaving skin hydrated and glowing.</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/08221038/LM-Insta-J.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"e65c517b-6089-5e79-9d47-6dca9c4492ac"}}

